import { Injectable } from '@angular/core';
import { ICallActivityDescriptor } from './process-element.types';

@Injectable({
  providedIn: 'root'
})
export class RxCallActivityRegistryService {
  private callActivityDescriptors: Map<string, ICallActivityDescriptor> = new Map();

  getRegisteredCallActivities(): IterableIterator<ICallActivityDescriptor> {
    return this.callActivityDescriptors.values();
  }

  register(...callActivityDescriptors: ICallActivityDescriptor[]): void {
    callActivityDescriptors.forEach((descriptor) => {
      this.callActivityDescriptors.set(descriptor.processDefinitionName, descriptor);
    });
  }
}
